<template>
  <div class="placeorder">
    <hx-Return :title="title"></hx-Return>
    <div class="placeorder-content">
      <!-- 选择收货地址 -->
      <div class="receive" v-show="!addressshow" @click="route">新增地址</div>
      <div class="placeorder-address" @click="isshow = !isshow" v-show="addressshow">
        <div class="name-number">
          <span>{{addresslist.name}}</span>:
          <span>{{addresslist.tel}}</span>
        </div>
        <div class="van-ellipsis van-ellipsis">{{addresslist.tit}}</div>
      </div>

      <!-- 订单内容 -->
      <div class="order-content">
        <div class="item" v-for="(item,index) in placeorderlist" :key="index">
          <div class="item-img">
            <img :src="item.url" alt />
          </div>
          <div class="item-tit van-multi-ellipsis--l2">{{item.title}}</div>
          <div class="item-price">
            <div class="item-pro">
              ￥
              <span>{{item.price}}</span>
            </div>
            <div class="pro-tit van-ellipsis">{{item.specifications}}</div>
            <div class="count">
              x
              <span>{{item.num}}</span>
            </div>
          </div>
        </div>
        <div class="order-shipping">
          <div class="order-service">
            <span>配送服务</span>
            <span class="service-right">快递运输</span>
          </div>
          <div class="order-ustify">
            <span>中小件送货时</span>
            <span class="ustify-right">工作日、双休日与节假日均可送货</span>
          </div>
        </div>
      </div>

      <div class="order-checkout">
        <ul>
          <li>
            <span>发票信息</span>
            <span class="checkout-right bl">个人商品明细</span>
          </li>
          <li>
            <span>礼品卡</span>
            <span class="checkout-right">无可用</span>
          </li>
          <li>
            <span>红包</span>
            <span class="checkout-right">无可用</span>
          </li>
        </ul>
      </div>
      <van-popup
        v-model="show"
        class="payment-password"
        position="bottom"
        closeable
        @close="falpayment"
      >
        <van-password-input
          :value="Paymentpassword"
          info="密码为 6 位数字"
          :focused="showKeyboard"
          :error-info="errorInfo"
        />
        <van-number-keyboard :show="showKeyboard" @input="onInput" @delete="onDelete" />
      </van-popup>

      <van-action-sheet v-model="isshow" title="请选择收货地址">
        <div class="content">
          <van-address-list
            v-model="chosenAddressId"
            :list="list"
            default-tag-text="默认"
            @click-item="select"
            @add="route"
          />
        </div>
      </van-action-sheet>
      <van-submit-bar text-align="left" :price="price" button-text="支付" @submit="showPopup" />
    </div>
  </div>
</template>

<script>
import hxReturn from "../components/hxReturn";
export default {
  data() {
    return {
      // 支付
      Paymentpassword: "",
      showKeyboard: true,
      show: false,
      errorInfo: "", //支付错误提示
      // 提交
      title: "提交订单",
      isshow: false, //选择地址
      addressshow: false, //默认地址为空
      chosenAddressId: "1",
      payment: false, //是否支付后跳转,
      routeaddress: false, //是否跳转新增地址
      list: [],
      placeorderlist: [],
      price: 0,
      // 地址
      addresslist: {
        name: "",
        tel: "",
        tit: ""
      }
    };
  },
  methods: {
    // 新增地址
    route() {
      this.routeaddress = true;
      this.$router.push({ name: "add" });
    },
    // 修改地址
    select(e) {
      // console.log(e.id);
      this.addresslist.name = this.list[e.id - 1].name;
      this.addresslist.tel = this.list[e.id - 1].tel;
      this.addresslist.tit = this.list[e.id - 1].address;
      this.list.forEach(v => {
        v.isDefault = false;
      });
      this.list[e.id - 1].isDefault = true;
    },
    // 支付成功
    add() {
      let obj = {
        orderID: "202001016204",
        address: this.addresslist,
        goodlist: this.placeorderlist,
        time: new Date().getTime(),
        timeDate: new Date(),
        price: this.price,
        type: 2 //已支付
      };
      this.payment = true;
      this.$store.commit("order/add", obj);
      this.$router.back(-1);
    },
    // 显示支付
    showPopup() {
      if (this.list.length < 1) {
        this.$toast("请设置收货地址");
        this.$router.push({ name: "add" });
        return;
      }
      if (this.$store.state.Paymentpassword == "") {
        this.$toast("请设置支付密码");
        this.$router.push({ name: "paymentsettings" });
        return;
      }

      this.show = true;
    },

    // 支付密码
    onInput(key) {
      this.Paymentpassword = (this.Paymentpassword + key).slice(0, 6);
      if (this.Paymentpassword.length === 6) {
        if (this.Paymentpassword == this.$store.state.Paymentpassword) {
          this.errorInfo = "";
          this.$toast("订单已提交");
          this.add();
        } else {
          this.errorInfo = "密码错误";
        }
      } else {
        this.errorInfo = "";
      }
    },
    // 密码变化
    onDelete() {
      this.Paymentpassword = this.Paymentpassword.slice(
        0,
        this.Paymentpassword.length - 1
      );
    },
    // 未支付成功就退出
    falpayment() {
      this.$toast("订单未支付");
      this.$store.commit("address/edit", this.list);
      this.$store.state.address.chosenAddressId = this.chosenAddressId;
      let obj = {
        orderID: "202001016204",
        address: this.addresslist,
        goodlist: this.placeorderlist,
        time: new Date().getTime(),
        timeDate: new Date(),
        price: this.price,
        type: 1 //待支付
      };
      this.$store.commit("order/add", obj);
      this.$router.back(-1);
    }
  },
  created() {
    // 默认地址为空
    this.routeaddress = false;
    // 未支付
    this.payment = false;
    // 隐藏底部导航
    this.$store.state.vantTab = false;
    // 获取vuex数据
    this.list = this.$store.state.address.lists;
    this.chosenAddressId = this.$store.state.address.chosenAddressId;
    this.placeorderlist = this.$store.state.tobepaidlist.obj;
    this.price = this.$store.state.tobepaidlist.price;
    // 判断是否有地址
    if (this.list.length < 1) {
      this.addressshow = false;
    } else {
      this.addressshow = true;
      // 获取默认地址
      this.list.forEach(v => {
        if (v.isDefault) {
          this.addresslist.name = v.name;
          this.addresslist.tel = v.tel;
          this.addresslist.tit = v.address;
        }
      });
    }
    //隐藏导航
  },
  // 切换组件生周期钩子，销毁函数
  beforeDestroy() {
    // 地址不为空 支付后  非新增地址 才执行
    // if (this.addressshow && !this.payment && !this.routeaddress) {
    //   this.$store.commit("address/edit", this.list);
    //   this.$store.state.address.chosenAddressId = this.chosenAddressId;
    //   let obj = {
    //     orderID: "202001016204",
    //     address: this.addresslist,
    //     goodlist: this.placeorderlist,
    //     time: new Date().getTime(),
    //     timeDate: new Date(),
    //     type: 1 //待支付
    //   };
    //   this.$store.commit("order/add", obj);
    //   this.$router.back(-1);
    // }
  },

  components: {
    hxReturn
  }
};
</script>

<style lang="less" scoped>
.placeorder {
  height: cacl(100% - 44px);
  width: 100%;
  padding-top: 44px;
  .placeorder-content {
    width: calc(100% - 20px);
    height: 100%;
    padding: 10px 10px 0 10px;
    .receive {
      background-color: #ffffff;
      font-size: 16px;
      color: #444;
      height: 60px;
      line-height: 60px;
      padding-left: 10px;
    }
    .order-content {
      margin-top: 10px;
      border-radius: 10px;
      background-color: #ffffff;
      .item {
        background-color: #ffffff;
        overflow: hidden;
        div {
          float: left;
        }
        .item-img {
          width: 80px;
          height: 80px;
          img {
            width: 100%;
            height: 100%;
          }
        }
        .item-tit {
          height: 40px;
          margin-top: 10px;
          margin-left: 10px;
          width: calc(100% - 120px);
          font-size: 14px;
        }
        .item-price {
          margin-left: 10px;
          width: calc(100% - 120px);
          .item-pro {
            color: red;
          }
          .count {
            float: right;
            position: relative;
            top: 3px;
            font-size: 13px;
          }
          .pro-tit {
            font-size: 12px;
            width: 100px;
            margin-left: 20px;
            margin-top: 3px;
          }
        }
      }
      .order-shipping {
        height: 55px;
        margin-top: 10px;
        padding: 10px 40px 10px 10px;
        border-top: 1px solid #f6f6f6;
        .service-right {
          float: right;
          font-size: 12px;
        }
        .order-ustify {
          margin-top: 10px;
          font-size: 13px;
          color: #999999;
          .ustify-right {
            float: right;
          }
        }
      }
    }
    .content {
      /deep/ .van-icon-edit::before {
        content: "";
      }
    }
  }
  .placeorder-address {
    height: 60px;
    width: calc(100% - 20px);
    background-color: #ffffff;
    padding-left: 20px;
    overflow: hidden;
    color: #5f6368;
    .name-number {
      margin-top: 10px;
      color: black;
      font-weight: 700;
      font-size: 16px;
    }
    .van-ellipsis{
      margin-top: 2px;
      font-size: 14px;
    }
  }
  .order-checkout {
    margin-top: 10px;
    background-color: #ffffff;
    border-radius: 10px;
    li {
      height: 43px;
      border-top: 1px solid #f6f6f6;
      line-height: 44px;
      padding-left: 20px;
      padding-right: 30px;
      .checkout-right {
        float: right;
        font-size: 12px;
        color: #5f6368;
      }
      .bl {
        color: black;
      }
    }
  }
  .payment-password {
    width: 100%;
    height: 500px;
    padding-top: 50px;
  }
}
</style>